<template>
    <div class="exchange">
        <div class="page">
            <header>
                <router-link to="/benefit">
                    <i class="el-icon-arrow-left"></i>
                </router-link>
                <span>兑换红包</span>
            </header>

            <form class="exchange_code">
                <el-input type="text" placeholder="请输入兑换码" class="exchange_input" v-model="exCode"></el-input>
                <section class="input_container captcha_code_container">
                    <el-input type="text" placeholder="验证码" maxlength="4" v-model="capCode" class="input_c"></el-input>
                    <div class="img_change_img">
                        <img :src="capUrl" class="img1">
                        <div class="change_img" @click="changeCap">
                            <p class="p1">看不清</p>
                            <p class="p2">换一张</p>
                        </div>
                    </div>
                </section>
            </form>
            <div class="alet_container  animated bounceIn" v-if="isShow2">
                <section class="tip_text_container">
                    <div class="tip_icon">
                        <span class="tip_icon_s1"></span>
                        <span class="tip_icon_s2"></span>
                    </div>
                    <p class="tip_text">无效的兑换码</p>
                    <div class="confrim" @click="isShow2 = !isShow2">确认</div>
                </section>
            </div>
            <div class="alet_container  animated bounceIn" v-if="isShow3">
                <section class="tip_text_container">
                    <div class="tip_icon">
                        <span class="tip_icon_s1"></span>
                        <span class="tip_icon_s2"></span>
                    </div>
                    <p class="tip_text">无效的兑换码</p>
                    <div class="confrim" @click="isShow3 = !isShow3">确认</div>
                </section>
            </div>
            <div class="determine" :style="{'background-color': isShow1 ? '#ccc' : '#4cd964'}" @click="exchange">兑换</div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Exchange",
        data(){
            return {
                capCode:"",
                capUrl:"",
                isShow1:false,
                isShow2:false,
                isShow3:false,
                exCode:"",
                user_id:"",
                exchange_code:"",
                captcha_code:""
            }
        },
        created(){
            this.changeCap();
            this.user_id = JSON.parse(localStorage.getItem('user_id'));
        },
        methods:{
            changeCap(){
                this.axios.post("/v1/captchas").then(res=>{
                    this.capUrl = res.code
                    // console.log(res);

                }).catch(err=>{});
            },
            exchange(){
                this.axios.post("/v1/users/"+ this.user_id + "/hongbao/exchange",{
                    params:{
                        exchange_code:this.exchange_code,
                        captcha_code:this.captcha_code
                    }
                }).then(res=>{
                    // console.log(res);
                    if(res.message == "无效的兑换码"){
                        this.isShow2 = true;
                    }else if(res.message == "请输入验证码"){
                        this.isShow3 = true;
                    }else {
                        this.isShow2 = false;
                        this.isShow3 = false;
                    }
                }).catch(err=>{});


            }
        }
    }
</script>

<style scoped>
    .exchange{
         width: 100%;
         overflow: hidden;
     }
    .page{
        padding-top: 1.95rem;
    }

    a{
        text-decoration: none;
    }
    header>a{
        color: #fff;
        position: absolute;
        top: 50%;
        left: .4rem;
        transform: translateY(-50%);
    }
    header span{
        font-size: .8rem;
        color: #fff;
        font-weight: 700;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
    }

    .exchange_code{
        margin-top: .7rem;
        padding: 0 .5rem;
    }
    .exchange_input{
        width: 97%;
        font-size: .7rem;
        color: #666;
        padding: .6rem .4rem;
        border-radius: .15rem;
        outline: none;

    }
    .input_container{
        height: 2.2rem;
        display: flex;
        margin-top: .7rem;
    }
    .input_c{
        font-size: .7rem;
        color: #666;
        padding: .4rem;
        border-radius: .15rem;
        flex: 3;
        outline: none;
    }
    .img_change_img{
        display: flex;
        align-items: center;
        flex: 2;
        margin-left: .3rem;
        background-color: #fff;
        padding-left: .2rem;
        border-radius: .15rem;
    }
    .img1{
        width: 3.2rem;
        height: 1.5rem;
        margin-right: .2rem;
    }
    .change_img{
        display: flex;
        flex-wrap: wrap;
        width: 2rem;
        justify-content: center;
    }
    .p1{
        font-size: .55rem;
        color: #666;
    }
    .p2{
        color: #3b95e9;
        font-size: .55rem;
        margin-top: .3rem;
    }
    .determine{
        background-color: #ccc;
        font-size: .7rem;
        color: #fff;
        text-align: center;
        margin: 0 .7rem;
        line-height: 1.8rem;
        border-radius: .2rem;
        margin-top: .7rem;
    }

    .alet_container{
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 200;
    }
    .tip_text_container{
        position: absolute;
        top: 50%;
        left: 50%;
        margin-top: -6rem;
        margin-left: -6rem;
        width: 12rem;
        animation: tipMove .4s;
        background-color: #fff;
        padding-top: .6rem;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        border: 1px;
        border-radius: .25rem;
    }
    .tip_icon{
        width: 3rem;
        height: 3rem;
        border: .15rem solid #f8cb86;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }
    .tip_icon_s1{
        width: .12rem;
        height: 1.5rem;
        background-color: #f8cb86;
    }
    .tip_icon_s2{
        width: .2rem;
        height: .2rem;
        border: 1px;
        border-radius: 50%;
        margin-top: .2rem;
        background-color: #f8cb86;
    }
    .tip_text{
        font-size: .7rem;
        color: #333;
        line-height: .9rem;
        text-align: center;
        margin-top: .8rem;
        padding: 0 .4rem;
    }
    .confrim{
        font-size: .8rem;
        color: #fff;
        font-weight: 700;
        margin-top: .8rem;
        background-color: #4cd964;
        width: 100%;
        text-align: center;
        line-height: 1.8rem;
        border: 1px;
        border-bottom-left-radius: .25rem;
        border-bottom-right-radius: .25rem;
    }
</style>